import React, { useRef, useState } from "react";
import "../qr.css";
import { toDataURL } from "qrcode";

import icon from "../qr-code.svg";
import error from "../exclamation-circle.svg";
import cancelIcon from "../x.svg";
import downloadIcon from "../download.svg";
export default function QrCodeCard() {
    const inputUrl = useRef();
    const [url, setUrl] = useState("");
    const [qrLoadStatus, setQrLoadStatus] = useState(false);
    const [errorMsg, setErrorMsg] = useState("");
    const [errorDisplay, setError] = useState(false);
    const [imgUrl, setImgUrl] = useState("");

    const validateUrl = (e) => {
        try {
            const newUrl = new URL(e);
            return newUrl.protocol === "http:" || newUrl.protocol === "https:";
        } catch (err) {
            return false;
        }
    };

    const changeErrorMsg = (e) => {
        setErrorMsg(e);
    };

    const changeImgUrl = (e) => {
        setImgUrl(e);
    };
    const changeErr = (e) => {
        setError(e);
    };

    const changeUrl = (e) => {
        setUrl(e.target.value);
    };

    const close = () => {
        setTimeout(() => {
            changeErr(false);
        }, 5000);
    };

    const getQR = () => {
        if (url === "") {
            changeErrorMsg("Enter the Url");
            changeErr(true);
            close();
        } else {
            if (validateUrl(url)) {
                changeErr(false);
                changeQrLoadStatus(true);
                displayQR();
            } else {
                changeErrorMsg("Enter Valid Url");
                changeErr(true);
                close();
                changeQrLoadStatus(false);
            }
        }
    };

    const changeQrLoadStatus = (e) => {
        setQrLoadStatus(e);
    };

    const cancel = () => {
        changeQrLoadStatus(false);
        setUrl("");
        changeImgUrl("");
        inputUrl.current.value = "";
    };

    const displayQR = async () => {
        try {
            const imgUrl = await toDataURL(url);
            changeImgUrl(imgUrl);
        } catch (err) {
            console.error(err);
        }
    };

    const enter = (e) => {
        if (e.keyCode === 13) {
            getQR();
        }
    };

    return (
        <div className="container">
            {!qrLoadStatus ? (
                <div className="qr-code">
                    <h3>Your QR</h3>
                    <h1>Generator</h1>
                </div>
            ) : (
                <div className="qr-code">
                    <img src={imgUrl} alt="qr-code" height={1750} width={350} />
                </div>
            )}
            <div className="input">
                <input
                    ref={inputUrl}
                    type="url"
                    name="url"
                    id="url"
                    placeholder="Enter your URL"
                    onChange={changeUrl}
                    onKeyDown={enter}
                />

                {!qrLoadStatus ? (
                    <button onClick={getQR}>
                        <img src={icon} alt="" />
                        Generate
                    </button>
                ) : (
                    <div className="options">
                        <button onClick={cancel} className="cancel">
                            <img src={cancelIcon} alt="" />
                            Cancel
                        </button>
                        <button className="download">
                            <a
                                href={imgUrl}
                                download="qrCode.png"
                                className="w-full"
                            >
                                <img src={downloadIcon} alt="" />
                                Download
                            </a>
                        </button>
                    </div>
                )}
            </div>
            {errorDisplay ? (
                <div className="error">
                    <img src={error} alt="" />
                    {errorMsg}
                </div>
            ) : (
                ""
            )}
        </div>
    );
}
